import React, { useEffect } from "react"
import { AnimatedCursor } from "../../../components/utils/AnimatedCursor"
import AOS from "aos"
import "aos/dist/aos.css"
import Layout from "../../../components/Layout"
import BlogOne from "../../../components/Blog/BlogsList/BlogOne"
import BlogTwo from "../../../components/Blog/BlogsList/BlogTwo"
import BlogThree from "../../../components/Blog/BlogsList/BlogThree"

export default function BlogsPage(req, res) {
  const blogsList = [
    {
      id: 1,
      blog: <BlogOne />,
    },
    {
      id: 2,
      blog: <BlogTwo />,
    },
    {
      id: 3,
      blog: <BlogThree />,
    },
  ]
  const filteredBlog = blogsList.filter(blog => blog.id == req.params.id)
  useEffect(() => {
    AOS.init()
  }, [])
  const mouseCoords =
    typeof window !== "undefined" &&
    JSON.parse(window.localStorage.getItem("mouseCoords"))
  console.log(filteredBlog)
  console.log(req.params.id)

  return (
    <Layout>
      {filteredBlog[0]?.blog}
      <AnimatedCursor
        coord={mouseCoords}
        innerSize={16}
        outerSize={16}
        color="255, 255, 255"
        outerAlpha={0.2}
        innerScale={0.8}
        outerScale={4}
        x
        clickables={[
          "a",
          'input[type="text"]',
          'input[type="email"]',
          'input[type="number"]',
          'input[type="submit"]',
          'input[type="image"]',
          "label[for]",
          "select",
          "textarea",
          "button",
          ".link",
          ".swiper-pagination-bullet",
        ]}
      />
    </Layout>
  )
}
